.hero {
  height: 200svh;
  position: relative;
}

.hero__bg {
  background-image: image-set("hero.155abb3c.webp" 1x type("image/webp"), "hero.7f5fc438.jpg" 1x type("image/jpeg"));
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100svh;
  position: absolute;
  inset: 0;
}

.hero__video-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.hero__video-wrapper:before {
  content: "";
  z-index: 1;
  background-color: #0006;
  position: absolute;
  inset: 0;
}

.hero__video {
  object-fit: cover;
  object-position: top center;
  width: 100%;
  height: 100%;
}

.hero__inner {
  width: 100%;
  height: 100svh;
  padding-bottom: 50px;
  position: sticky;
  top: 0;
}

.hero__content {
  z-index: 2;
  justify-content: space-between;
  align-items: end;
  width: 100%;
  display: flex;
  position: relative;
}

.hero__body {
  z-index: 1;
  height: 100%;
  display: flex;
  position: relative;
}

.hero__title {
  font-family: var(--font-family-third);
  text-transform: uppercase;
  color: var(--base-content-100);
  letter-spacing: .02em;
  width: 100%;
  max-width: 880px;
  font-size: 40px;
  font-weight: 700;
  line-height: 140%;
}

.hero__description {
  text-transform: uppercase;
  text-align: center;
  color: var(--base-content-200);
  margin: 40px auto 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  display: none;
}

.hero__button.button {
  text-transform: none;
  text-wrap: nowrap;
  white-space: nowrap;
  height: 58px;
  padding: 24px;
  font-size: 16px;
  font-weight: 500;
}

@media (width <= 1024px) {
  .hero__inner {
    padding-bottom: 40px;
  }
}

@media (width <= 768px) {
  .hero__content {
    flex-direction: column;
    justify-content: end;
  }

  .hero__title {
    text-align: center;
    font-size: 30px;
  }

  .hero__description {
    width: 100%;
    max-width: 174px;
    display: inline-block;
  }

  .hero__button {
    width: 100%;
    margin: 42px auto 0;
  }

  .hero__video {
    object-position: center center;
  }
}
